

const DistributionSelector = ({ distributions, selectedDistribution, handleDistributionSelect, parameters, increments, handleParamChange, resample }) => {
  return (
    <div className="distributionSelector">
      <div className="selectorItem">
        <table>
          <tbody>
          <tr key="distribution">
            <td>Distribution</td>
            <td>
              <select
                value={selectedDistribution}
                onChange={handleDistributionSelect}>

                {distributions.map((distribution) => (
                  <option key={distribution.name} value={distribution.name}>
                    {distribution.name}
                  </option>
                ))}
              </select>
            </td>
          </tr>
          {Object.keys(parameters).map((key, index) => (
            <tr key={key}>
              <td>{key}</td>
              <td><input
                type="number"
                value={parameters[key] ?? defaultValue}
                step={increments[index]}
                onChange={(e) => handleParamChange(key, e.target.value)}
              /></td>
            </tr>
          ))}
          </tbody>
        </table>
      </div>
      <div className="selectorItem">
        <button id="resampleButton" onClick={resample}><b>Sample</b></button>
      </div>
    </div>
  );
};

export default DistributionSelector;
