// src/App.js
import React, { useState, useEffect } from "react"
import Histogram from "./Histogram"
import DistributorSelector from "./DistributorSelector"
import {
  randint,
  uniform,
  normal,
  cauchy,
  exponential,
  chisquare,
  poisson,
  geometric,
  pareto,
  binomial,
  triangular,
  logo
} from "randlibjs"
import "./App.css"

const SAMPLING_SIZE = 5000
const DEFAULT_DISTRIBUTION = 0
const LOGO_SIZE = 64
const BINS = 100
const LOGO_TIMEOUT = 1000
const DISCRETE_INCREMENT = 1
const CONTINUOUS_INCREMENT = 0.05

const DISTRIBUTIONS = [
  { name: "chisquare", sampler: parameters => chisquare(parameters.k, parameters.size), parameters: { k: 6.0 }, increments: [DISCRETE_INCREMENT] },
  { name: "randint", sampler: parameters => randint(parameters.low, parameters.high, parameters.size), parameters: { low: 0.0, high: 6.0 }, increments: [DISCRETE_INCREMENT, DISCRETE_INCREMENT] },
  { name: "uniform", sampler: parameters => uniform(parameters.low, parameters.high, parameters.size), parameters: { low: 0.0, high: 1.0 }, increments: [CONTINUOUS_INCREMENT, CONTINUOUS_INCREMENT] },
  { name: "normal", sampler: parameters => normal(parameters.loc, parameters.scale, parameters.size), parameters: { loc: 0.0, scale: 1.0 }, increments: [CONTINUOUS_INCREMENT, CONTINUOUS_INCREMENT] },
  { name: "cauchy", sampler: parameters => cauchy(parameters.median, parameters.gamma, parameters.size), parameters: { median: 0.0, gamma: 1.0 }, increments: [CONTINUOUS_INCREMENT, CONTINUOUS_INCREMENT] },
  { name: "exponential", sampler: parameters => exponential(parameters.lambda, parameters.size), parameters: { lambda: 1.0 }, increments: [CONTINUOUS_INCREMENT] },
  { name: "poisson", sampler: parameters => poisson(parameters.lambda, parameters.size), parameters: { lambda: 1.0 }, increments: [CONTINUOUS_INCREMENT] },
  { name: "geometric", sampler: parameters => geometric(parameters.p, parameters.size), parameters: { p: 0.5 }, increments: [CONTINUOUS_INCREMENT] },
  { name: "pareto", sampler: parameters => pareto(parameters.alpha, parameters.xm, parameters.size), parameters: { alpha: 1.0, xm: 1.0 }, increments: [CONTINUOUS_INCREMENT, CONTINUOUS_INCREMENT] },
  { name: "binomial", sampler: parameters => binomial(parameters.n, parameters.p, parameters.size), parameters: { n: 10.0, p: 0.5 }, increments: [DISCRETE_INCREMENT, CONTINUOUS_INCREMENT] },
  { name: "triangular", sampler: parameters => triangular(parameters.a, parameters.b, parameters.c, parameters.size), parameters: { a: 0.0, b: 1.0, c: 0.5 }, increments: [CONTINUOUS_INCREMENT, CONTINUOUS_INCREMENT, CONTINUOUS_INCREMENT] },
]

function computeHistogram(muestra, numCajas) {
  muestra.sort((a, b) => a - b);
  const min = muestra[0];
  const max = muestra[muestra.length - 1];
  const rango = max - min;
  const tamanoCaja = rango / numCajas;
  const cajas = Array(numCajas).fill(0);

  muestra.forEach(valor => {
    const indiceCaja = Math.min(Math.floor((valor - min) / tamanoCaja), numCajas - 1);
    cajas[indiceCaja]++;
  });

  // Generate labels
  const labels = [];
  for (let i = 0; i < numCajas; i++) {
    const start = min + i * tamanoCaja;
    const end = start + tamanoCaja;
    labels.push(`${start.toFixed(1)}-${end.toFixed(1)}`);
  }

  return {
    labels,
    values: cajas,
  };
}

function getSamplerForDistribution(distributionName) {
  return DISTRIBUTIONS.find(distribution => distribution.name === distributionName).sampler
}

function copyToClipboard(id) {
  const codeElement = document.querySelector(id);
  const range = document.createRange();
  range.selectNode(codeElement);
  window.getSelection().removeAllRanges();
  window.getSelection().addRange(range);
  try {
    document.execCommand('copy');
  } catch (err) {
    // pass
  }
  window.getSelection().removeAllRanges();
}

const App = () => {
  const [selectedDistribution, setSelectedDistribution] = useState(DISTRIBUTIONS[DEFAULT_DISTRIBUTION].name);
  const [selectedParameters, setSelectedParameters] = useState(DISTRIBUTIONS[DEFAULT_DISTRIBUTION].parameters);
  const [selectedIncrements, setSelectedIncrements] = useState(DISTRIBUTIONS[DEFAULT_DISTRIBUTION].increments);
  const [sample, setSample] = useState(() => {
    return getSamplerForDistribution(selectedDistribution)({ ...selectedParameters, size: SAMPLING_SIZE })
  });
  const [svgString, setSvgString] = useState(logo(LOGO_SIZE));

  // Functions
  function handleDistributionSelect(e) {
    const distributionName = e.target.value
    setSelectedDistribution(distributionName)
    const newSelectedDistribution = DISTRIBUTIONS.find(distribution => distribution.name === distributionName)
    setSelectedParameters(newSelectedDistribution.parameters)
    setSelectedIncrements(newSelectedDistribution.increments)
    resample(newSelectedDistribution.name, newSelectedDistribution.parameters)
  }
  function handleParamChange(name, value) {
    const newParameters = { ...selectedParameters, [name]: Number(value) }
    setSelectedParameters(newParameters);
    resample(selectedDistribution, newParameters)
  };
  function resample(distributionName, parameters) {
    const sampler = getSamplerForDistribution(distributionName)
    try {
      const newSample = sampler({ ...parameters, size: SAMPLING_SIZE })
      setSample(newSample)
    }
    catch (error) {
      setSample(null)
      console.log("Parmetro incorrecto.")
    }
  }

  let histogram = null
  if (sample) {
    histogram = computeHistogram(sample, BINS);
  }
  const histogramContent = <Histogram data={histogram} />

  // Change logo dynamically
  const updateLogo = () => {
    setSvgString(logo(32));
  };
  const updateFavicon = () => {
    // Crea un Blob del SVG para generar una URL
    const blob = new Blob([svgString], { type: "image/svg+xml" });
    const blobUrl = URL.createObjectURL(blob);

    // Encuentra el favicon actual o crea uno nuevo
    let favicon = document.querySelector('link[rel="icon"]');
    if (!favicon) {
      favicon = document.createElement("link");
      favicon.rel = "icon";
      document.head.appendChild(favicon);
    }

    // Actualiza el atributo href del favicon
    favicon.href = blobUrl;

    // Limpia la URL del Blob anterior para evitar fugas de memoria
    return () => URL.revokeObjectURL(blobUrl);
  };
  useEffect(() => {
    updateLogo(); // Genera el logo inicialmente
    const interval = setInterval(updateLogo, LOGO_TIMEOUT); // Actualiza en cada intervalo
    return () => clearInterval(interval); // Limpia el intervalo al desmontar el componente
  }, [LOGO_TIMEOUT]);
  useEffect(() => {
    updateFavicon(); // Genera el logo inicialmente
    const interval = setInterval(updateFavicon, LOGO_TIMEOUT); // Actualiza en cada intervalo
    return () => clearInterval(interval); // Limpia el intervalo al desmontar el componente
  }, [LOGO_TIMEOUT]);

  return (
    <>
      <div className="App">
        <header className="fixed-header">
          <div className="container header-container">
            <div className="logo-container">
              <span id="logo" dangerouslySetInnerHTML={{ __html: logo(64) }} />
              <a href="https://pereradrian.github.io/randlibjs" className="library-link">Randlibjs</a>
            </div>
            <nav className="main-nav">
              <ul>
                <li><a href="#installation">Installation</a></li>
                <li><a href="#usage">Usage</a></li>
                <li><a href="#showcase">Showcase</a></li>
                <li><a href="https://pereradrian.github.io/randlibjs/docs" target="_blank" rel="noopener noreferrer">Docs</a></li>
                <li><a href="https://www.npmjs.com/package/randlibjs" target="_blank" rel="noopener noreferrer">NPM</a></li>
                <li><a href="https://github.com/pereradrian/randlibjs" target="_blank" rel="noopener noreferrer">GitHub</a></li>
              </ul>
            </nav>
            <button className="mobile-menu-toggle" aria-label="Toggle menu">
              <span></span>
              <span></span>
              <span></span>
            </button>
          </div>
        </header>
        
        <div className="main-content">
          <section className="hero-section">
            <h1 className="main-title">Powerful Lightweight Random Number Generators</h1>
            <p className="lead">
              Randlibjs is a powerful JavaScript library designed to provide a wide range of random number distributions efficiently.
            </p>
            <div className="cta-buttons">
              <a href="#installation" className="cta-button primary">Get Started</a>
              <a href="#showcase" className="cta-button secondary">Try it Now</a>
            </div>
          </section>

          <section id="installation" className="content-section">
            <h2>Installation</h2>
            <p>
              To get started with Randlibjs, you can install it via npm. Simply run the following command in your terminal:
            </p>
            <div className="code-box">
              <code id="npm-command">npm install randlibjs</code>
              <button className="copy-btn" onClick={() => copyToClipboard('#npm-command')} aria-label="Copy to clipboard">
                <span className="tooltip">Copy</span>
              </button>
            </div>
          </section>

          <section id="usage" className="content-section">
            <h2>Usage</h2>
            <p>
              After installing the library, you can import it into your project and start generating random numbers. Here's an example of how to generate random numbers using a standard normal distribution:
            </p>
            <div className="code-box">
              <code id="example-code">
                import normal from 'randlibjs';<br /><br />

                // Generate a sample from a standard normal distribution<br />
                const sample = normal(low=0.0, high=1.0, size=10);<br /><br />

                // Output result sample<br />
                console.log(sample);
              </code>
              <button className="copy-btn" onClick={() => copyToClipboard('#example-code')} aria-label="Copy to clipboard">
                <span className="tooltip">Copy</span>
              </button>
            </div>
            <div className="features-grid">
              <div className="feature-card">
                <h3>Multiple Distributions</h3>
                <p>Support for various statistical distributions including normal, uniform, chi-square, and more.</p>
              </div>
              <div className="feature-card">
                <h3>Efficient Algorithms</h3>
                <p>Optimized for performance with lightweight implementation.</p>
              </div>
              <div className="feature-card">
                <h3>Easy Integration</h3>
                <p>Simple API that works seamlessly with your JavaScript projects.</p>
              </div>
            </div>
          </section>

          <section id="showcase" className="content-section">
            <h2>Showcase</h2>
            <p>
              On this page, you can interactively generate random numbers and visualize the results with a dynamic histogram. Simply select a distribution and watch the sample data update in real time.
            </p>
            <div className="showcase-container">
              <DistributorSelector 
                distributions={DISTRIBUTIONS}
                selectedDistribution={selectedDistribution}
                handleDistributionSelect={handleDistributionSelect}
                parameters={selectedParameters}
                increments={selectedIncrements}
                handleParamChange={handleParamChange}
                resample={() => resample(selectedDistribution, selectedParameters)} 
              />
              {histogramContent}
            </div>
          </section>
          
          <section className="additional-info">
            <h2>Additional Features</h2>
            <p>
              The library supports various statistical distributions, giving flexibility for simulations, games, data analysis, and more. While the showcase here focuses on random number generation and histogram visualization, Randlibjs also offers additional powerful methods such as:
            </p>
            <ul className="features-list">
              <li>Seeding for reproducible results</li>
              <li>Array shuffling algorithms</li>
              <li>Permutation generation</li>
              <li>Custom distribution creation</li>
            </ul>
          </section>
        </div>

        <div className="npm-link-container">
          <p>
            Visit official Randlibjs npm page at <a href="https://www.npmjs.com/package/randlibjs" className="library-link" target="_blank" rel="noopener noreferrer">npmjs.com/package/randlibjs</a>.
          </p>
        </div>
      </div>

      <footer className="footer">
        <div className="footer-content">
          <div className="footer-section">
            <h3>Randlibjs</h3>
            <p>A powerful JavaScript library for random number generation.</p>
          </div>
          <div className="footer-section">
            <h3>Links</h3>
            <ul>
              <li><a href="https://www.npmjs.com/package/randlibjs">NPM Package</a></li>
              <li><a href="https://github.com/pereradrian/randlibjs">GitHub Repository</a></li>
              <li><a href="https://pereradrian.github.io">Author's Website</a></li>
            </ul>
          </div>
          <div className="footer-section">
            <h3>Contact</h3>
            <p>Have questions or suggestions? Open an issue on GitHub.</p>
          </div>
        </div>
        <div className="copyright">
          <small>Designed by <a href="https://pereradrian.github.io" target="_blank" rel="noopener noreferrer">Adrin Muoz Perera</a></small>
        </div>
      </footer>
    </>
)

};

export default App;
